<template>
	<fx-item-row-edit
		:title="itemData.goodsDTO.name"
		@on-back="onCloseClick"
	>
		<van-cell-group>
			<w-cell title="">{{itemData.goodsDTO.code}}</w-cell>
			<w-cell title="">{{itemData.goodsDTO.name}}</w-cell>
			<w-cell title="">{{itemData.goodsDTO.spec}}</w-cell>
			<w-cell title="">{{itemData.mainUnit}}</w-cell>
			<w-cell title="">{{itemData.price}}</w-cell>
			<w-cell title="">{{itemData.busUnitOutAmount}}</w-cell>
			<w-cell title="">{{itemData.outMoney}}</w-cell>
			<w-cell v-if="itemData.assistUnit" title="">{{itemData.assistUnit}}</w-cell>
			<w-cell v-if="itemData.assistUnit" title="">{{itemData.outAssistAmount}}</w-cell>
			<w-cell title="">{{$fxUtils.setDateTime(new Date(itemData.productDate))}}</w-cell>
			<w-cell title="">{{itemData.batchCode}}</w-cell>
			<w-cell title="">{{itemData.mainUnit}}</w-cell>
			<w-cell title="">{{itemData.mainAmount}}</w-cell>
			<van-pro-field
				ref="memo"
				v-model="itemData.memo"
				:placeholder="setPlaceholder('')"
				:disabled="isDisabled"
				label=""
				input-align="right"
				@focus="onFocus('memo')"
			/>
		</van-cell-group>
	</fx-item-row-edit>
</template>

<script>
export default {
	name: 'itemRowEdit',
	props: {
		visibleFlag: Boolean,
		itemData: {
			type: Object,
			default: () => {
				return {}
			}
		},
		isDisabled: Boolean
	},
	computed: {
		setPlaceholder (val) {
			return (val) => {
				return this.isDisabled ? '' : val
			}
		}
	},
	methods: {
		onCloseClick () {
			this.$emit('update:visibleFlag', false)
		},
		onFocus (ref) {
			this.$fxUtils.doScrollTop(this.$refs[ref].$el)
		}
	}
}
</script>
